<script>
  import * as axios from "axios";
  
  export default {
    name: 'Start',
    props: {},
    data() {
      return {
        img_src: require("../assets/noimage.png"),
        responseData: null,
        shuffledWord: null,
        hiddenWord: [],
        clickCounter: 0,
        wordsCounter: 0,
        modal: false,
        about: false,
      }
    },
    
    mounted: function () {
      this.getData();
    },
    computed: {},
    methods: {
      addToTable() {
      
      },
      showAbout() {
        return this.about = !this.about;
      },
      hideModal() {
        this.modal=false;
        this.getData();
      },
      createHiddenWord() {
        this.clickCounter = 0;
        let tempArr = [];
        for (let i = 0; i < this.shuffledWord.length; i++) {
          tempArr[i] = '?';
        }
        this.hiddenWord = tempArr;
      },
      updateHiddenWord(e) {
        if (this.clickCounter < this.shuffledWord.length) {
          this.$set(this.hiddenWord, this.clickCounter, e.target.textContent);
          this.clickCounter++;
        }
        if (this.clickCounter === this.shuffledWord.length) {
          if (this.hiddenWord.join('') === this.responseData.name.toUpperCase()) {
            
            this.modal = true;
          }
        }
      },
      conLog(e) {
      
      },
      getData() {
        this.img_src = require("../assets/noimage.png");
        let randomNumber = Math.floor(Math.random() * (1368 - 1) + 2);
        let url = "https://apidir.pfdo.ru/v1/directory-program-activities/" + 1259;
        console.log(url);
        axios
          .get(url)
          .then(response => {
            if (response.data.result_message === 'Запись не найдена') {
              this.getData();
            } else {
              console.log(response.data);
              this.responseData = response.data.data;
              if (response.data.data.img_src) {
                this.img_src = response.data.data.img_src;
              }
            }
          })
          .then(() => {
            this.shuffle(this.responseData.name);
          })
          .then(() => {
            this.createHiddenWord();
          })
        // .catch(function (error) {
        //   console.log(error);
        // })
      },
      //Ф-я перемешивания символов в строке
      shuffle(str) {
        let arr = str.split('');
        let j, temp;
        for (let i = arr.length - 1; i > 0; i--) {
          j = Math.floor(Math.random() * (i + 1));
          temp = arr[j];
          arr[j] = arr[i];
          arr[i] = temp;
        }
        return this.shuffledWord = arr.join('');
      },
    }
  }
</script>

